<template>
  <div id="app">
    <div class="container">
      <div class="row clear-fix">
        <div class="col-lg-12 col-md-12 col-sm-12 col-sx-12">
          <md-field class="fine-adjustment">
          <label>Input Your momo Share Website</label>
             <md-input v-model="site" ></md-input>
          </md-field>
          <md-button class=" md-raised md-primary" @click="send" >刷吧</md-button>
           <md-field>
             <label>{{goodword}}</label>
             <md-textarea v-model="result" disabled></md-textarea>
          </md-field>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "app",
  data() {
    return {
      msg: "",
      autogrow:"",
      goodword:'坚持总会被听见，做自己心中的英雄，加油',
      result:'',
      site: ''
    };
  },
  methods: {
    send : function(){
      var _this = this;
      // this.result = "aaaa"
      if( _this.site === '' || _this.site.length <= 0) { return  }
      var params = { 'site' : _this.site}
      this.$axios.post('http://localhost:3000/go',params).then(function (data) {
        console.log(JSON.stringify(data))
        _this.result = "收到 : " + data.data.count;

      }).catch(function (err) {
        console.log("Maybe come across some  errors")
      })

    }
  }
};
</script>

<style lang="scss" scoped>
@import "~vue-material/dist/theme/engine";

@include md-register-theme("default", (
  primary: md-get-palette-color(blue, A200), // The primary color of your application
  accent: md-get-palette-color(red, A200), // The accent or secondary color

));

@import "~vue-material/dist/theme/all";

.fine-adjustment {
  margin-bottom: 5px;
}


</style>




